import {Core, Fetch, ToastService} from '@true/core';
import {batch} from 'react-redux';
import {
  EOrderUpdate,
  ETypeUpdate,
  ICreatePoll,
  ICreatePost,
  IPoll,
  IPollPost,
  IPost,
} from '@/store/updates/type';
import {getUpdates, setUpdatesQuery, syncUpdates} from '@/store/updates/index';
import {IEvent} from '@/store/events/type';
import {IAnnouncement} from '@/store/announcements/type';
import {IUser} from '@true/core';
import {IChannel} from '@/store/channels/type';
import {setChannelsQuery} from '@/store/channels';
import {setTokenUpdate} from '@/zustand/TokenUpdate';

interface IGetUpdatesParams {
  order: EOrderUpdate;
}
export const requestUpdatePosts = async (params: IGetUpdatesParams) => {
  const {data} = await Fetch.postWithToken<{
    updates: (IPost & IAnnouncement & IEvent)[];
    pinned_updates: (IPost & IAnnouncement & IEvent)[];
    ptoken: string;
  }>(`@${Core.systemPath}/inside/ajax/api/update/gets?order=${params?.order}`);

  batch(() => {
    if (data.updates.length) {
      setTokenUpdate(data.ptoken);
      syncUpdates([...data.updates, ...data['pinned_updates']]);
      setUpdatesQuery(prev => ({
        ...prev,
        ['all']: data.updates.map(item => item.id.toString()),
        ['pinned']: data.pinned_updates.map(item => item.id.toString()),
        [ETypeUpdate.IDEA]: data.updates
          .filter(item => item.truetype === ETypeUpdate.IDEA)
          .map(item => item.id.toString()),
        [ETypeUpdate.ANNOUNCEMENT]: data.updates
          .filter(item => item.truetype === ETypeUpdate.ANNOUNCEMENT)
          .map(item => item.id.toString()),
        [ETypeUpdate.EVENT]: data.updates
          .filter(item => item.truetype === ETypeUpdate.EVENT)
          .map(item => item.id.toString()),
        [ETypeUpdate.POLL]: data.updates
          .filter(item => item.truetype === ETypeUpdate.POLL)
          .map(item => item.id.toString()),
        [ETypeUpdate.STYLED]: data.updates
          .filter(item => item.truetype === ETypeUpdate.STYLED)
          .map(item => item.id.toString()),
        [ETypeUpdate.UPDATE]: data.updates
          .filter(item => item.truetype === ETypeUpdate.UPDATE)
          .map(item => item.id.toString()),
        [ETypeUpdate.WELCOME]: data.updates
          .filter(item => item.truetype === ETypeUpdate.WELCOME)
          .map(item => item.id.toString()),
      }));
    }
  });
  return data.updates;
};

export const requestUpdatePost = async (params: {
  update_id: string | number;
}) => {
  const {data} = await Fetch.postWithToken<{
    update: IPost & IAnnouncement & IEvent;
  }>(
    `@${Core.systemPath}/inside/ajax/api/update/get?update_id=${params.update_id}`,
  );

  batch(() => {
    if (data.update) {
      syncUpdates([data.update]);
    }
  });
  return data.update;
};

export const requestUpdatePostsChannel = async (params: {
  path: string;
  order: EOrderUpdate;
}) => {
  const {data} = await Fetch.postWithToken<{
    updates: (IPost & IAnnouncement & IEvent)[];
    pinned_updates: (IPost & IAnnouncement & IEvent)[];
    ptoken: string;
  }>(
    `@${Core.systemPath}/inside/ajax/api/channel/get?path=${params.path}&order=${params.order}`,
  );
  batch(() => {
    if (data.updates.length) {
      setTokenUpdate(data.ptoken);
      syncUpdates([...data.updates, ...data['pinned_updates']]); //sau nay se bo
      setUpdatesQuery(prev => ({
        ...prev,
        [params.path]: data.updates.map(item => item.id.toString()),
        [`pinned_${params.path}`]: data.pinned_updates.map(item =>
          item.id.toString(),
        ),
      }));
    }
  });
  return data.updates;
};

export const requestUserProfile = async (params: {username: string}) => {
  const {data} = await Fetch.postWithToken<{
    updates: (IPost & IAnnouncement & IEvent)[];
    user: IUser;
    appuser: {cover: string};
    upath: 'profile';
    channels: IChannel[];
    ptoken: string;
  }>(
    `@${Core.systemPath}/inside/ajax/api/profile/get?tail=saved&username=${params.username}`,
  );
  batch(() => {
    if (data.updates.length) {
      setTokenUpdate(data.ptoken);
      syncUpdates(data.updates);
      setUpdatesQuery(prev => ({
        ...prev,
        [params.username]: data.updates.map(item => item.id.toString()),
      }));
    }
    if (data.channels.length) {
      // syncChannels(data.channels)
      setChannelsQuery(prev => ({
        ...prev,
        [params.username]: data.channels.map(item => item.id.toString()),
      }));
    }
  });
  return;
};

export const requestCreatePost = async (params: ICreatePost) => {
  const {data} = await Fetch.postWithToken<{
    message: string;
    update: (IPost & IEvent & IAnnouncement) | IPollPost;
  }>(`@${Core.systemPath}/inside/ajax/api/update/post`, params);
  batch(() => {
    if (data.message === 'ok') {
      syncUpdates([data.update]);
      setUpdatesQuery(prev => ({
        ...prev,
        ['all']: [data.update.id.toString(), ...prev.all],
      }));
      ToastService.show('Create post successfully');
    }
  });
  return data?.message;
};

export const requestEditPost = async (params: ICreatePost) => {
  const {data} = await Fetch.postWithToken<{
    message: string;
    update: (IPost & IEvent & IAnnouncement) | IPollPost;
  }>(`@${Core.systemPath}/inside/ajax/api/update/edit`, params);
  batch(() => {
    if (data.update) {
      const update = getUpdates(params?.id || '');
      syncUpdates([
        {
          ...update,
          ...data.update,
        },
      ]);

      console.log('Edit success post', data.update);
    }
  });
  return data?.message;
};

export const requestRemovePost = async (params: {
  id: number;
  metatype: string;
  pathChannel?: string;
}) => {
  const {data} = await Fetch.postWithToken<{code: number}>(
    `@${Core.systemPath}/inside/ajax/api/update/remove`,
    params,
  );
  batch(() => {
    if (data.code) {
      setUpdatesQuery(prev => ({
        ...prev,
        ['all']: [...prev.all.filter(id => id !== params.id.toString())],
      }));
      if (params?.pathChannel) {
        setUpdatesQuery(prev => ({
          ...prev,
          // @ts-ignore
          [params.pathChannel]: [...prev.all.filter(id => id !== params.id)],
        }));
      }
      console.log('remove success post');
    }
  });
  return;
};

export const requestReaction = async ({
  hid,
  token,
  reaction,
}: {
  hid: string;
  token: string;
  reaction: string;
}) => {
  const {data} = await Fetch.postWithToken<{
    origin: IPost & IAnnouncement & IEvent;
  }>(`@${Core.systemPath}/inside/ajax/api/like/reaction`, {
    hid,
    token,
    reaction,
  });
  batch(() => {
    if (data.origin) {
      const update = getUpdates(data.origin['update_id']);
      if (!update?.attachment) {
        syncUpdates([data.origin]);
      } else {
        // @ts-ignore
        syncUpdates([{...update, attachment: data.origin}]);
      }
      console.log('reaction successfully');
    }
  });
  return data?.origin;
};

