export const requestAddPollOptions = async (params: {
  name: string;
  id: number;
  channel_id: number;
}) => {
  const {data} = await Fetch.postWithToken<{poll: IPoll; update: IPost}>(
    `@${Core.systemPath}/inside/ajax/api/poll/options`,
    {
      ['options[]']: params.name,
      id: params.id,
      channel_id: params.channel_id,
    },
  );
  batch(() => {
    if (data?.poll) {
      const _update = getUpdates(data.update.id);
      // @ts-ignore
      syncUpdates([{..._update, attachment: data.poll}]);
    }
  });
};
