import constate from 'constate';
import {useSharedValue} from 'react-native-reanimated';
import {useMemo} from 'react';

const _provider = () => {
  const imageLayout = useSharedValue<{[id: string]: {w: number; h: number}}>(
    {},
  );
  return useMemo(
    () => ({
      imageLayout,
    }),
    [imageLayout],
  );
};

export const [PostImageProvider, usePostImage] = constate(
  _provider,
  val => val,
);
