import {setupStore} from '@true/core';
import {EventsReducer, setEventsStore} from '@/store/events';
import {ChannelsReducer, setChannelsStore} from '@/store/channels';
import {
  AnnouncementsReducer,
  setAnnouncementsStore,
} from '@/store/announcements';
import {setUpdatesStore, UpdatesReducer} from '@/store/updates';
import {LettersReducer, setLettersStore} from '@/store/letters';
import {setTopicsStore, TopicsReducer} from '@/store/topics';
import {CoreValuesReducer, setCoreValuesStore} from '@/store/corevalues';
import {SavedReducer, setSavedStore} from '@/store/saved';
import {setSettingStore, settingReducer} from '@/store/settings';


export const insideReducers = {
  events: EventsReducer,
  channels: ChannelsReducer,
  announcements: AnnouncementsReducer,
  updates: UpdatesReducer,
  letters: LettersReducer,
  topics: TopicsReducer,
  corevalues: CoreValuesReducer,
  saved: SavedReducer,
  setting: settingReducer,
}
export const setInsideStore = (store: any) => {
  setEventsStore(store);
  setChannelsStore(store);
  setAnnouncementsStore(store);
  setUpdatesStore(store);
  setLettersStore(store);
  setTopicsStore(store);
  setTopicsStore(store);
  setCoreValuesStore(store);
  setSavedStore(store);
  setSettingStore(store);
}
export const {persistor} = setupStore(insideReducers, setInsideStore)

