import React, {memo} from 'react';
import {addLangResources, AppInfo, lazily} from '@true/core';
import {MyMiniApp} from '@true/ui';
import {persistor} from '@/store';
import {requestUpdatePosts} from '@/store/updates/function';
import {
  requestAllChannels,
  requestJoinedChannels,
} from '@/store/channels/function';
import {Routes} from '@/Routes';
import {requestSettingApp} from '@/store/settings/function';
import {EOrderUpdate} from '@/store/updates/type';
import {PostImageProvider} from '@/components/PostThumbnail/postImageProvider';

/* -- setup app info -- */
AppInfo.title = 'Inside';
AppInfo.subTitle = 'A social network to connect and engage across the company';
AppInfo.primaryColor = '#3AA394';
AppInfo.preSyncData = async () => {
  await requestSettingApp();
  await requestAllChannels();
  await requestJoinedChannels();
  await requestUpdatePosts({order: EOrderUpdate.UPDATE});
};

/* -- add languages -- */
addLangResources({
  en: {...require('@/assets/languages/en.json')},
  vi: {...require('@/assets/languages/vi.json')},
}).then();

const MiniApp = memo(() => {
  return (
    <PostImageProvider>
      <MyMiniApp component={Routes} persistor={persistor} />
    </PostImageProvider>
  );
});

export default MiniApp;
