import {Core, Fetch, ToastService} from '@true/core';
import {batch} from 'react-redux';
import {ETypeChannel, IChannel, ICreateChannel} from '@/store/channels/type';
import {
  getChannels,
  getChannelsByQuery,
  setChannelsQuery,
  syncChannels,
} from '@/store/channels/index';

export const requestAllChannels = async () => {
  const {data} = await Fetch.postWithToken<{
    channels: IChannel[];
  }>(`@${Core.systemPath}/inside/ajax/api/channel/gets`);

  batch(() => {
    if (data.channels.length) {
      syncChannels(data.channels);
      setChannelsQuery(prev => ({
        ...prev,
        ['all']: data.channels.map(item => item.id.toString()),
        [ETypeChannel.TEAM]: data.channels
          .filter(item => item.metatype === ETypeChannel.TEAM)
          .map(item => item.id.toString()),
        [ETypeChannel.COMPANY]: data.channels
          .filter(item => item.metatype === ETypeChannel.COMPANY)
          .map(item => item.id.toString()),
      }));
    }
  });
  return data.channels;
};

