export const requestChannelPath = async (params: {path: string}) => {
  const {data} = await Fetch.postWithToken<{
    channel: IChannel;
  }>(`@${Core.systemPath}/inside/ajax/api/channel/get?path=${params.path}`);

  batch(() => {
    if (data.channel) {
      syncChannels([data.channel]);
    }
  });
  return data.channel;
};

export interface IAddChannelMember {
  id: string;
  users: string;
}
export const requestAddMemberChannel = async (params: IAddChannelMember) => {
  const {data} = await Fetch.postWithToken<{
    channel: IChannel;
  }>(`@${Core.systemPath}/inside/ajax/api/channel/member/add`, params);
  batch(() => {
    if (data.channel) {
      syncChannels([data.channel]);
      ToastService.show('Add member success');
    }
  });
};
