export const requestCreateChannel = async (params: ICreateChannel) => {
  const {data} = await Fetch.postWithToken<{
    channel: IChannel;
  }>(`@${Core.systemPath}/inside/ajax/api/channel/create`, params);
  batch(() => {
    if (data.channel) {
      syncChannels([data.channel]);
      setChannelsQuery(prev => ({
        ...prev,
        ['all']: [data.channel.id.toString(), ...prev.all],
      }));
    }
  });
};
