import {Core, Fetch} from '@true/core';
import {batch} from 'react-redux';
import {IAppuser, ISetting, ISystemInfo} from '@/store/settings/type';
import {syncSetting} from '@/store/settings/index';

export const requestSettingApp = async () => {
  const {data} = await Fetch.postWithToken<{
    appuser: IAppuser;
    dark_mode: number;
    sysinfo: ISystemInfo;
  }>(`@${Core.systemPath}/inside/ajax/api/setting/info`);

  batch(() => {
    if (data.sysinfo) {
      const setting: ISetting = {
        appuser: data.appuser,
        darkmode: data.dark_mode,
        sysinfo: data.sysinfo,
      };
      syncSetting(setting);
    }
  });
  return data.sysinfo;
};

export interface IEditCompany {
  title?: string;
  logo?: string;
  image?: string;
  address?: string;
  vision?: string;
  founded?: number;
  mv_sticky?: string;
}
export const initEditCompany: IEditCompany = {
  title: '',
  logo: '',
  image: '',
  address: '',
  vision: '',
};
export const requestEditCompanyInfo = async (param: IEditCompany) => {
  const {data} = await Fetch.postWithToken<{
    appuser: IAppuser;
    dark_mode: number;
    sysinfo: ISystemInfo;
  }>(`@${Core.systemPath}/inside/ajax/api/company/edit`, param);

  batch(() => {
    if (data.sysinfo) {
      const setting: ISetting = {
        appuser: data.appuser,
        darkmode: data.dark_mode,
        sysinfo: data.sysinfo,
      };
      syncSetting(setting);
    }
  });
  return data.sysinfo;
};
